.gallery{
    &_head{
        padding-top: 70px;
        text-align: center;
        background-color: #000;
        min-height: 200px;
        @media screen and (max-width: 960px) {
        padding: 10px;
        }
        &__title{
            font-weight: bold;
            text-transform: uppercase;
            color: #f16522;
            font-size: 48px;
        }
        &__text{
            margin-top: 20px;
            color: #fff;
        }
    }
    &_body{
        min-height: 860px;
        background-image: url(../img/galery-bg.png);
    }
    
}

.showcase{
padding-top: 60px;
display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    &_item{
        text-align: center;       
        margin: 2em auto;    
        background: rgba(255, 255, 255, 0.8);
        @media screen and (max-width: 960px) {
        }
        &_content{
            padding: 20px;
            opacity: 0.8;
        &__title{
            padding-bottom: 10px;
            font-size: 20px;
            color: #f16522;
            font-weight: bolder;
        }
            &__price{
                padding-top: 10px;
                font-size: 20px;
            color: #f16522;
            font-weight: bolder;
            }
            
        }
        &__image{
            height: 181px;
            padding: 0px;
            margin: 0px;
        }
    }
}