@import "variables";
$breakpoint-phone: 480px !default;
$breakpoint-tablet: 768px !default;
$breakpoint-desktop: 1024px !default;
.cf:after {
  content: "";
  display: table;
  clear: both;
}
.wrapper{
    margin: 0px 5%;
}
.page{
    margin: 0px;
    padding: 0px;
    font-family: 'Roboto', sans-serif;
}

.header{
    text-align: center;
    background-color: #000;
}
.menu{
    min-height: 100px;
    padding: 0px;
    margin: 0px;
    list-style: none;
    &__row{
        display: inline-block;
    }
    &__link{
        padding: 10% 40px;
        color: #78858e;
        text-decoration: none;
        font-size: 16px;
        text-transform: uppercase;
        &-active{
            color: #fff;
        }
    }
    &__logo{
        position: relative;
        top: 15px;
    }
}
@media screen and (max-width: 960px) {
    .menu {
        &__row{
            display: block;
            padding: 20px 0px;
            text-align: center;
            &:nth-child(4){
                display: none;
            }
        }
        &__link{
            font-weight: bolder;
        }
        
    }
}

.slider{
    color: #fff;
    text-align: center;
    width: 100%;
    background-image: url(../img/slider-image.png);
    padding: 20% 0;
    &__title{
        text-transform: uppercase;
        font-weight: bold;
        font-size: 59px;
    }
    &__explanation{
        font-size: 20px;
    }
}

.info{
    height: 560px;
    &_content{
        padding-top: 150px;
        padding-left: 70px;
        float: left;
        width: 50%;
        @media screen and (max-width: 960px) {
        width: 100%;
        text-align: center;
        padding: 20px 0px 0px 0px;
        float:none;
        }

        &__title{
            color: #f16522;
            font-size: 50px;
        }
        &__button{
            display: inline-block;
            text-decoration: none;
            text-transform: uppercase;
            color: #fff;
            background-color: #f16522;
            padding: 15px 20px;
            font-size: 18px;
        }
    }
    &__image{
        width: 30%;
        min-height: 540px;
        background-repeat: no-repeat;
        background-image: url(../img/chef.png);
        background-position: 50% 35%;
        padding-top: 50px;
        padding-right: 70px;
        float: right;
        @media screen and (max-width: 960px) {
        display: none;
        float: none;
        }
    }

}

